import ScrollReveal from "scrollreveal";

export default function scrollInit() {
  const sr = ScrollReveal();
  
  const optionsTop = {
    distance: "100%",
    origin: "top",
    opacity: 0,
    mobile: false,
    delay: 200,
  };
  
  const optionsBottom = {
    distance: "20%",
    origin: "bottom",
    opacity: 0,
    mobile: false,
  };
  
  const optionsOpacity = {
    opacity: 0,
    mobile: false,
    delay: 400,
    afterReveal: function(domEl) {
      const $counter = $(domEl).find('.counter span');
      if ($counter.length) {
        animateCounter($counter);
      }
    }
  };

  const optionsOpacity2x = {
    opacity: 0,
    mobile: false,
    delay: 1200,
  };

  const optionsTopDelay = {
    distance: "20%",
    origin: "top",
    opacity: 0,
    mobile: false,
    delay: 800,
  };

  sr.reveal(".reveal-from-top", optionsTop);
  sr.reveal(".reveal-from-bottom", optionsBottom);
  sr.reveal(".reveal-from-opacity", optionsOpacity);
  sr.reveal(".reveal-from-opacity-2x", optionsOpacity2x);
  sr.reveal(".reveal-from-opacity-delay", optionsTopDelay);
}

function animateCounter($counter) {
  const targetNumber = parseInt($counter.data('value'), 10);
  const duration = 2000;
  let startTimestamp = null;

  function step(timestamp) {
    if (!startTimestamp) startTimestamp = timestamp;
    const progress = Math.min((timestamp - startTimestamp) / duration, 1);
    const easeProgress = progress * (2 - progress);
    const currentNumber = Math.round(targetNumber * easeProgress);
    
    $counter.text(currentNumber);
    
    if (progress < 1) {
      window.requestAnimationFrame(step);
    } else {
      $counter.text(targetNumber);
    }
  }

  window.requestAnimationFrame(step);
}
if ($(window).width() < 1100) {
  animateCounter($('.counter span'));
}