import Swiper from "swiper";
import { Navigation, Pagination } from 'swiper/modules';

export default function sliderHero() {
  const swiper = new Swiper('.swiper-hero', {
    modules: [Pagination, Navigation],
    spaceBetween: 10,
    slidesPerView: 1.02,
    pagination: {
      el: '.swiper-pagination',
      type: 'bullets',
      clickable: true,
    },
    breakpoints: {
      1100: {
        slidesPerView: 3,
      },
    },
    navigation: {
      prevEl: '.swiper-button-prev',
      nextEl: '.swiper-button-next'
    },
    on: {
      init: function () {
        updateActiveTip(this.activeIndex);
      },
      slideChange: function () {
        updateActiveTip(this.activeIndex);
      }
    }
  });

  // Функция для обновления активного .hero__tip
  function updateActiveTip(activeIndex) {
    const tips = document.querySelectorAll('.hero__tip');
    
    // Удаляем 'active' у всех
    tips.forEach(tip => tip.classList.remove('active'));

    // Добавляем 'active' к соответствующему tip
    if (tips[activeIndex]) {
      tips[activeIndex].classList.add('active');
    }
  }
}

sliderHero();